.keyboard {
  width: 700px;
  height: 300px;
  margin: 0 auto;
  margin-top: 40px;

}

.line1,
.line2,
.line3 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}

.key {
  display: grid;
  place-items: center;
  width: 50px;
  height: 70px;
  border-radius: 4px;
  margin: 5px;
  background-color: gray;
  cursor: pointer;
}

#big {
  width: 100px;
}

#used {
  background-color: var(--primary-color);
}